<template>
 <div class="clearfix">
    <div class="interviewroomlist">
        <div class="header"><span>面试间</span></div>
        <div class="btn_header clearfix">
        <el-select v-model="svalue" class="" placeholder="请选择">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <el-button type="primary" @click="dialogVisible = true" class="add">新增面试间</el-button>
        </div>
        <div class="contentlist">
            <ul>
                <li>
                    <div class="first">
                      <div><span class="hspan">产品面试间</span><img  class="edit_img" src="../assets/images/edit.png" @click="editRoom"/><img  class="del_img" src="../assets/images/delete.png" @click="delRoom"/></div>
                      <div><img  class="cimg" src="../assets/images/clock.png"/><span>2018/01/25 09:00开始</span></div>
                      <div><img  class="cimg" src="../assets/images/clock.png"/><span>每人面试30min</span></div>
                      <div><img  class="aimg" src="../assets/images/allpoper.png"/><span>30人</span></div>
                      <div class="popoer"><img  class="cimg" src="../assets/images/poper.png"/><span >闫小晶</span></div>
                      <div><el-button type="primary">进入面试间<img  class="btn_in" src="../assets/images/right.png"/></el-button></div>
                    </div>
                    <div class="poperlist">
                        <ul :style="{width:swidth}">
                            <li :style="{marginLeft:getMleft}">
                              <div><span class="name">闫小景1</span><img class="ximg" src="../assets/images/female.png"/><span class="isonline">在线</span></div>  
                              <div><span>2018/01/25  09:00</span><img  class="edit_img" src="../assets/images/edit.png" @click="editPoper"/></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div class="ubtn"><el-button type="primary">去面试</el-button><el-button type="primary">通知</el-button></div>
                            </li>
                             <li>
                              <div><span class="name">闫小景2</span><img class="ximg" src="../assets/images/female.png"/><span class="isonline">在线</span></div>  
                              <div><span>2018/01/25  09:00</span><img class="ximg" src="../assets/images/female.png"/></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div class="ubtn"><el-button type="primary">去面试</el-button><el-button type="primary">通知</el-button></div>
                            </li>
                             <li>
                              <div><span class="name">闫小景3</span><img class="ximg" src="../assets/images/female.png"/><span class="isonline">在线</span></div>  
                              <div><span>2018/01/25  09:00</span><img class="ximg" src="../assets/images/female.png"/></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div class="ubtn"><el-button type="primary">去面试</el-button><el-button type="primary">通知</el-button></div>
                            </li>
                            <li>
                              <div><span class="name">闫小景4</span><img class="ximg" src="../assets/images/female.png"/><span class="isonline">在线</span></div>  
                              <div><span>2018/01/25  09:00</span><img class="ximg" src="../assets/images/female.png"/></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div><span>清华大学 | 应用化学 | 本科</span></div>
                              <div class="ubtn"><el-button type="primary">去面试</el-button><el-button type="primary">通知</el-button></div>
                            </li>
                        </ul>
                        <div class="zzc" @click="mleft"></div>
                        <div class="yzc" @click="mright"></div>
                    </div>
                </li>
                <li></li>
            </ul>
        </div>
        <div class="fpage">
            <el-pagination
            :background="background"
            :pageSize="pageSize"
            layout="prev, pager, next , jumper , -> , slot"
            :total="1000"
            prev-text="上一页"
            next-text="下一页" 
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            class="pagination"
            >
            </el-pagination>
        </div>
        <el-dialog :title="curtitle" :visible.sync="dialogVisible">
            <el-form :model="form" :rules="rules" ref="form">
                <el-form-item label="面试间名称：" :label-width="formLabelWidth"  prop="name">
                <el-input v-model="form.name" :style="{width:defaultWidth}" placeholder="面试间名称" auto-complete="off"></el-input><span class="oleft">1-20个字符</span>
                </el-form-item>
                <el-form-item label="面试开始时间：" :label-width="formLabelWidth"  prop="stime">
                   <el-date-picker
                    v-model="form.stime"
                    type="date"
                    :editable="false"
                    placeholder="面试开始时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="每人面试时长："  :label-width="formLabelWidth" prop="pdate">
                    <el-input-number v-model="form.pdate"  :min="1"  :style="{width:defaultWidth}" :max="999" label="每人面试时长"></el-input-number><span class="oleft">分钟</span>
                </el-form-item>
                <el-form-item label="面试人数：" :label-width="formLabelWidth"  prop="count">
                 <el-input-number v-model="form.count" :min="1"  :style="{width:defaultWidth}" :max="999" label="面试人数"></el-input-number>
                <p class="ytime">预计11：11面试结束</p>
                </el-form-item>
                <el-form-item label="面试官："  :label-width="formLabelWidth" prop="radio">
                    <el-radio-group v-model="form.radio" @change="radiochange($event)" >
                        <el-radio  label="1" >我自己</el-radio>
                        <el-radio  label="2">邀请面试官</el-radio>
                    </el-radio-group>
                 </el-form-item>
                <el-form-item prop="mname" :label-width="formLabelWidth">
                  <el-input v-model="form.mname"  :disabled="isme" placeholder="昵称" :style="{width:defaultWidth}" auto-complete="off"></el-input><span class="oleft">1-20个字符</span>
                </el-form-item>
                <el-form-item prop="memail" :label-width="formLabelWidth">
                  <el-input v-model="form.memail"  :disabled="isme" placeholder="面试官邮箱" :style="{width:defaultWidth}" auto-complete="off"></el-input><span class="oleft">面试官会收到面试任务</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveFormInterview">确 定</el-button>  
                <el-button @click="cancelForm">取 消</el-button>
            </div>
        </el-dialog>
    </div>
 </div>  
</template>
<script>
import {Button , Input,Dialog,Select,Form,FormItem,DatePicker,MessageBox } from 'element-ui';
import dicConfig from '../../static/dicConfig';
import axios from  'axios';
let edulist = dicConfig.educationDicList;
    
export default {
  name:'Interview',
  data(){
      var validateMname = (rule, value, callback) => {          
        if(this.isme==false){
            if (value === ''||this.replacestr(value,' ','')=='') {
            callback(new Error('请输入昵称'));
            } else {
            if (value.length>1&&value.length<20) {
                 callback();
            }else{
               callback(new Error('长度在 1 到 20 个字符')); 
            }
          }
         }else{
              callback();
         }
      };
      var validateMemail = (rule, value, callback) => {
          if(this.isme==false){
            if (value === ''||this.replacestr(value,' ','')=='') {
            callback(new Error('请输入面试官邮箱'));
            } else {
            var reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/; 
            if (reg.test(value)) {
                callback();
            }
            callback(new Error('面试官邮箱格式不正确'));
            }
          }else{
               callback();
          }
      };       
      return {
        options: [{
          value: '0',
          label: '全部面试间'
        }, {
          value: '1',
          label: '自己创建的'
        }, {
          value: '2',
          label: '被分配的'
        }],
        svalue: '0',
        edudic:edulist,
        dialogVisible:false,
        isme:true,
        background:true,
        swidth:'1920px',
        currentleft:0,
        currentPage:1,
        pageSize:10,
        form: {
            name: '',
            stime: '',
            pdate: '5',
            count: '30',
            radio:'1',
            mname:'',
            memail:''
        },
        rules:{
            name: [
            { required: true, message: '请输入面试间名称', trigger: 'blur' },
            { min:1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
          ],
            stime: [
            { type: 'date',required: true, message: '时间格式不正确', trigger: 'change' },
          ],
         radio: [
            {  required: true, message: '请选择面试官', trigger: 'change' }
          ],
          mname: [{validator: validateMname, trigger: 'blur' }],
          memail:[{validator: validateMemail, trigger: 'blur' }]
        },
        formLabelWidth: '140px',
        defaultWidth:'220px',
        curtitle:'新增面试间',
       
      }
  },
  components:{
    
  },
  methods:{
      cancelForm(){
        this.dialogVisible=false;
        this.$refs['form'].resetFields();
      },
    //替換指定字符
    replacestr(str,kstr,tstr)
    {
        if(!!str)
        {
          return str.replace(new RegExp('['+kstr+']','g'),tstr);
        }
        return '';
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
    },
    mleft(){
        if(this.currentleft>0){
            this.currentleft--;
            console.log(this.currentleft);
        }
    },
    mright(){
        if((this.currentleft+3)<4){
            this.currentleft++;
            console.log(this.currentleft);
        }
    },
      saveFormInterview(){
        this.$refs['form'].validate((valid) => {
          if (valid) {
            alert('submit!'); 
            console.log(this.form);
            this.$refs['form'].resetFields();
            this.dialogVisible=false;
            
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      radiochange(ev){
         if(ev==2){
             this.isme=false;
         }else{
             this.isme=true;
             this.form.mname='';
             this.form.memail='';
         }
      },
      editRoom(){

      },
      delRoom(){
        this.$confirm('删除面试间后则无法恢复，是否确认删除？', '删除面试间', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          console.log('取消')         
        });
      },
      editPoper(){

      }
  },
  computed:{
      getMleft:function(){
          let xleft=(this.currentleft%3==0&&this.currentleft!=0)?277:278;
          return '-'+this.currentleft*xleft+'px';
      }
  },
  created(){

  },
  mounted(){

  }
}
</script>
<style lang="less" scoped>
@import '../assets/css/interview.less';
</style>



